import { registerBlockType } from '@wordpress/blocks';
import metadata from './block.json';

/**
 * Internal dependencies
 */
import Edit from './edit';
import Save from './save';

// icon
import icon from './icon';
import saveDep from './deprecated';

/**
 * Block Registration
 */

registerBlockType(metadata, {
	icon: {
		src: icon,
		foreground: '#77b5f7',
	},
	edit: Edit,
	save: Save,
	deprecated: [
		{
			"attributes": {
				"anchorLinksShow":{
					"type": "boolean",
					"default": false
				},
				"QaStyle": {
					"type": "boolean",
					"default": false
				},
				"uniqueId": {
					"type": "string"
				},
				"activeAccordionBorder": {
					"type": "object",
					"default": {
						"width": "1px",
						"color": "#000000",
						"style": "solid"
					}
				},
				"searchShow":{
					"type": "boolean",
					"default": false
				},
				"showAllbtn":{
					"type": "boolean",
					"default": false
				},
				"closeText":{
					"type": "string",
					"default": "Close all"
				},
				"openText":{
					"type": "string",
					"default": "Show all"
				},
				"heading": {
					"type": "string",
					"default": "Accordion Heading"
				},
				"subheading": {
					"type": "string",
					"default": ""
				},
				"subheadingColor": {
					"type": "string"
				},
				"activetorClass": {
					"type": "string",
					"default": "click"
				},
				"faqSchema": {
					"type": "boolean",
					"default": false
				},
				"step": {
					"type": "boolean",
					"default": false
				},
				"stepText": {
					"type": "string",
					"default": "Continue"
				},
				"stepCmpltText": {
					"type": "string",
					"default": "Congratulations, you are done!"
				},
				"checkList": {
					"type": "boolean",
					"default": false
				},
				"buttonShow":{
					"type": "boolean",
					"default": false
				},
				"contentCount": {
					"type": "number",
					"default": 300
				},
				"readText":{
					"type": "string",
					"default": "Read More"
				},
				"border": {
					"type": "object",
					"default": {
						"width": "1px",
						"color": "#cccccc",
						"style": "solid"
					}
				},
				"paddings": {
					"type": "object",
					"default": {
						"top": "10px",
						"left": "15px",
						"right": "15px",
						"bottom": "10px"
					}
				},
				"margins": {
					"type": "object",
					"default": {
						"top": "0px",
						"bottom": "15px"
					}
				},
				"borderRadius": {
					"type": "number",
					"default": 0
				},
				"headingColor": {
					"type": "string"
				},
				"headerBg": {
					"type": "string",
				},
				"iconColor": {
					"type": "string"
				},
				"iconBackground": {
					"type": "string"
				},
				"bodyBg": {
					"type": "string"
				},
				"showIcon": {
					"type": "boolean",
					"default": true
				},
				"qIconText": {
					"type": "string",
					"default": "Q"
				},
				"qIconColor": {
					"type": "string",
					"default": "#fff"
				},
				"qIconBg": {
					"type": "string",
					"default": "#505050"
				},
				"aIconText": {
					"type": "string",
					"default": "A"
				},
				"aIconColor": {
					"type": "string",
					"default": "#fff"
				},
				"aIconBg": {
					"type": "string",
					"default": "#f5a623"
				},
				"customCSS": {
					"type": "string",
					"default": ""
				},
				"openALLBtnPosition": {
					"type": "string",
					"default": "right_btn"
				},
				"headingBorder": {
					"type": "object",
					"default": {
						"color": "undefined",
						"style": "solid",
						"width": "0px"
					}
				},
				"bodyBorder": {
					"type": "object",
					"default": {
						"top": {
							"color": "#cccccc",
							"style": "solid",
							"width": "1px"
						}
					}
				},
				"activeBackground": {
					"type": "string"
				},
				"categoryList" : {
					"type": "array"
				},
				"filterBtnAlignment": {
					"type": "string",
					"default": "center_btn"
				},
				"filterBtnPosition": {
					"type": "string",
					"default": "horizontal_filter"
				},
				"enableCategoryFilter": {
					"type": "boolean",
					"default": false
				},
				"showMoreBtn": {
					"type": "boolean",
					"default": false
				},
				"itemsToShow": {
					"type": "number",
					"default": 5
				},
				"showMoreBtnColor": {
					"type": "string",
					"default": "#010101"
				},
				"showMoreBtnBg": {
					"type": "string",
					"default": "#eaeaea"
				},

				"filterBtnColor": {
					"type": "string",
					"default": "#0866ff"
				},
				"filterBtnBg": {
					"type": "string",
					"default": "#10b3d608"
				},
				"filterBtnActiveColor": {
					"type": "string",
					"default": "#fff"
				},
				"filterBtnActiveBg": {
					"type": "string",
					"default": "#0866ff"
				}
			},
			migrate: (attributes) => {
				return {
					...attributes,
					paddings: {
						top: attributes.paddings.top === "10px" ? "" : attributes.paddings.top,
						right: attributes.paddings.right === "20px" ? "" : attributes.paddings.right,
						left: attributes.paddings.left === "20px" ? "" : attributes.paddings.left,
						bottom: attributes.paddings.bottom === "10px" ? "" : attributes.paddings.bottom
					},
					margins: {
						top: attributes.margins.top === "0px" ? "0px" : attributes.margins.top,
						bottom: attributes.margins.bottom === "15px" ? "15px" : attributes.margins.bottom
					},
					headingColor: attributes.headingColor || '',
					headerBg: attributes.headerBg || '',
				};
			},
			save: saveDep,
		},
	]
});
